import React from 'react';
import {FormControl, InputLabel, MenuItem, Select} from "@mui/material";

const SortCategory = ({title,value,setValue,}) => {
    const [genres, setGenres] = React.useState('');
    const [country, setCountry] = React.useState('');
    const [language, setLanguage] = React.useState('');
    const [sortBy, setSortBy] = React.useState('');
    const sortForm = [
        {
            title:'',
            value:genres,
            setValue:setGenres,
        },
        {
            title:'',
            value:country,
            setValue:setCountry,
        },
        {
            title:'',
            value:language,
            setValue:setLanguage,
        },
        {
            title:' :',
            value:sortBy,
            setValue:setSortBy,
        },
    ]
    const adf = [1,2,4,5,6]
    return (

        <>
            {sortForm.map((a ,index)=>
                <FormControl key={index} className="Genres">
                    <InputLabel id="demo-simple-select-label">{a.title}</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={a.value}
                        label=""
                        onChange={(event) => a.setValue(event.target.value)}
                    >
                        <MenuItem value={'ukraine'}></MenuItem>
                        <MenuItem value={'usa'}></MenuItem>
                        <MenuItem value={'poland'}></MenuItem>
                    </Select>
                </FormControl>)}

        </>

    )
};

export default SortCategory;